import io.github.terrakok.compose.wizard.ProjectFile

class AndroidManifest(info: AndroidProjectInfo) : ProjectFile {
    override val path = "app/src/main/AndroidManifest.xml"
    override val content = """
        <?xml version="1.0" encoding="utf-8"?>
        <manifest xmlns:android="http://schemas.android.com/apk/res/android">
        
            <application
                android:name=".${info.safeName}App"
                android:icon="@android:drawable/ic_menu_compass"
                android:label="@string/app_name"
                android:theme="@style/Theme.${info.safeName}">
               <activity
                    android:name=".MainActivity"
                    android:exported="true">
                    <intent-filter>
                        <action android:name="android.intent.action.MAIN" />
        
                        <category android:name="android.intent.category.LAUNCHER" />
                    </intent-filter>
                </activity>
            </application>
        
        </manifest>
    """.trimIndent()
}

